/* cube */
:root {
    --size: 250px;
}

.cube1 {
    width: 250px;
    width: var(--size);
    height: 250px;
    height: var(--size);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
   /* transform: rotateY(30deg) rotateZ(30deg); */
   -webkit-animation: spin 20s infinite linear;
           animation: spin 20s infinite linear;
}

.side1 {
    position: absolute;
    opacity: 0.9;
    width: 250px;
    width: var(--size);
    height: 250px;
    height: var(--size);
}

#front1 {
    background: url([object Module]) no-repeat center/cover;
    -webkit-transform: translateZ(calc(250px / 2));
            transform: translateZ(calc(250px / 2));
    -webkit-transform: translateZ(calc(var(--size) / 2));
            transform: translateZ(calc(var(--size) / 2));
}

#top1 {
    background: url([object Module]) no-repeat center/cover;
    -webkit-transform: rotateX(-270deg) translateY(calc(-1 * (250px / 2)));
            transform: rotateX(-270deg) translateY(calc(-1 * (250px / 2)));
    -webkit-transform: rotateX(-270deg) translateY(calc(-1 * (var(--size) / 2)));
            transform: rotateX(-270deg) translateY(calc(-1 * (var(--size) / 2)));
    -webkit-transform-origin: top center;
            transform-origin: top center;
}

#bottom1 {
    background: url([object Module]) no-repeat center/cover;
    -webkit-transform: rotateX(270deg) translateY(calc(250px / 2));
            transform: rotateX(270deg) translateY(calc(250px / 2));
    -webkit-transform: rotateX(270deg) translateY(calc(var(--size) / 2));
            transform: rotateX(270deg) translateY(calc(var(--size) / 2));
    -webkit-transform-origin: bottom center;
            transform-origin: bottom center;
}

#right1 {
    background: url([object Module]) no-repeat center/cover;
    -webkit-transform: rotateY(-270deg) translateX(calc(250px / 2));
            transform: rotateY(-270deg) translateX(calc(250px / 2));
    -webkit-transform: rotateY(-270deg) translateX(calc(var(--size) / 2));
            transform: rotateY(-270deg) translateX(calc(var(--size) / 2));
    -webkit-transform-origin: top right;
            transform-origin: top right;
}

#left1 {
    background: url([object Module]) no-repeat center/cover;
    -webkit-transform: rotateY(270deg) translateX(calc(-1 * (250px / 2)));
            transform: rotateY(270deg) translateX(calc(-1 * (250px / 2)));
    -webkit-transform: rotateY(270deg) translateX(calc(-1 * (var(--size) / 2)));
            transform: rotateY(270deg) translateX(calc(-1 * (var(--size) / 2)));
    -webkit-transform-origin: center left;
            transform-origin: center left;
}

#back1 {
    background: url([object Module]) no-repeat center/cover;
    -webkit-transform: rotateY(-180deg) translateZ(calc((250px / 2)));
            transform: rotateY(-180deg) translateZ(calc((250px / 2)));
    -webkit-transform: rotateY(-180deg) translateZ(calc((var(--size) / 2)));
            transform: rotateY(-180deg) translateZ(calc((var(--size) / 2)));
}

@-webkit-keyframes spin {
    from {
        -webkit-transform: rotateX(0) rotateY(0) rotateZ(0);
                transform: rotateX(0) rotateY(0) rotateZ(0);
    }
    to {
        -webkit-transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
                transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
    }
}

@keyframes spin {
    from {
        -webkit-transform: rotateX(0) rotateY(0) rotateZ(0);
                transform: rotateX(0) rotateY(0) rotateZ(0);
    }
    to {
        -webkit-transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
                transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
    }
}
